	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/css-selection-styles/index.md absPath of file >>> MarkdownRemark","html":"<p>Did you know you could customize the selection colours of html pages with CSS?  I didn't - until now.  Turns out, there's a <a href=\"http://www.w3.org/TR/css3-selectors/#UIfragments\" title=\"Open link in a new window\">CSS3 pseudo-element called ::selection</a>.  Naturally, this isn't yet a technique supported by browsers like Internet Explorer, but it is supported in [albeit with different syntax] by Firefox and Safari.  Interesting, eh?  Check an article on <a href=\"http://www.quirksmode.org/css/selection.html\" title=\"Open link in a new window\">CSS Selection Styles from Quirksmode</a>, and have a look at some of the creative effects you can get with it <a href=\"http://metaatem.net/selcolor.php\" title=\"Open link in a new window\">here</a>.</p>","frontmatter":{"title":"CSS Selection Styles?","date":"February 24, 2008","tags":["css"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/adobe-on-air-comes-to-london/","tags":["adobe","air","conferences"],"title":"Adobe on AIR comes to London...","date":"2008-02-23T15:59:34.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/trouble-migrating-to-actionscript-3/","tags":["actionscript"],"title":"Trouble migrating to Actionscript 3?","date":"2008-02-27T08:00:35.000Z","description":null,"image":null}}}}

